.body {
  padding: 1em;
  display: flex;
  flex-direction: column;
  align-self: center;
  justify-items: center; }

.body .animated {
  padding: 1em;
  margin-top: 20vh; }

@media only screen and (min-width: 900px) {
  .body .animated {
    width: 60%; } }

.greeting {
  font-size: clamp(3em, 8vw, 10em);
  letter-spacing: 1ex;
}

.header {
  position: absolute;
  top: 0;
  left: 0;

  height: 100vh;
  width: 100%;

  display: grid;
  grid-gap: 1em;
  grid-template-rows: repeat(5, -webkit-fit-content);
  grid-template-rows: repeat(5, fit-content);
  text-align: center;

  background-color: black;

  opacity: 1;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.item {
  opacity: 1;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.hidden {
  opacity: 0;
  pointer-events: none;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.toggle {
  color: white;
  position: absolute;
  right: 2em;
  top: 2em;
  z-index: 2;
  padding: 1em;
  cursor: pointer;
}

.hover {
  z-index: 2;
}

